import glob from 'glob';
import path from 'path';
import { getChildLogger } from '@zg-rentals/logger-node';
import { file } from '@zg-rentals/cli-tools';
import type { PackageJson, PackageJsonWithLocation } from './Workspace';
import Workspaces from './Workspaces';

export function resetWorkspaceCache() {
  cache = new Map<string, Workspaces>();
}

let cache: Map<string, Workspaces> = new Map<string, Workspaces>();

export function getWorkspaceGraph(repoRoot: string, logger = getChildLogger('Getting workspaces')): Workspaces {
  if (cache.has(repoRoot)) {
    logger.debug('Got workspaces from cache');
    return cache.get(repoRoot) as Workspaces;
  }

  const rootPackageJson = file.loadJsonSync<PackageJson>({ filepath: path.join(repoRoot, 'package.json'), logger });
  const { workspaces: workspaceGlobs = [] } = rootPackageJson;
  const workspaces: Array<PackageJsonWithLocation> = [];

  logger.trace(`Workspace globs:
${workspaceGlobs.map((g: string) => `  - ${g}`).join('\n')}`);

  workspaceGlobs.forEach((globString: string) => {
    glob.sync(globString, { cwd: repoRoot }).map((workspacePath) => {
      try {
        const packageJson = file.loadJsonSync<PackageJson>({
          filepath: path.join(repoRoot, workspacePath, 'package.json'),
          logger,
        });
        workspaces.push({ ...packageJson, location: path.join(repoRoot, workspacePath) });
      } catch (e) {
        if (!(e.path.endsWith('package.json') && e.code === 'ENOENT')) {
          throw e;
        }
      }
    });
  });

  const graph = new Workspaces({ ...rootPackageJson, location: repoRoot }, workspaces);

  cache.set(repoRoot, graph);
  return graph;
}
