import type { Request, Response } from 'express';
import type { Level, LogEvent } from 'pino';
import type { Logger } from '@zg-rentals/logger-base';

export function getLogMiddleware(logger: Logger) {
  return function logMiddleware(req: Request, res: Response) {
    const logs = req.body?.logs || req.body;

    if (logs && Array.isArray(logs)) {
      logs.forEach((logEvent: LogEvent) => {
        const { level, ts, messages, bindings } = logEvent;
        const bindingsAsObj = bindings.reduce((acc, binding) => {
          return {
            ...acc,
            ...binding,
          };
        }, {});

        bindingsAsObj.browser = true;

        const childLogger = logger.child(bindingsAsObj);

        if (messages[0]) {
          if (typeof messages[0] !== 'object') {
            messages.unshift({});
          }

          messages[0] = {
            ...messages[0],
            requestId: req.id,
            time: ts,
          };

          childLogger[level.label as Level](messages[0], ...messages.slice(1));
        }
      });
    }

    res.send('ok');
  };
}
