import type { Arguments, Argv, CommandBuilder, MiddlewareFunction, RequireDirectoryOptions } from 'yargs';
import type { DefaultArgv } from '.';

import getNodeLogger, { verbosityToLogLevel } from '@zg-rentals/logger-node';
import parser from 'yargs-parser';
import unparser from 'yargs-unparser';
import Yargs from 'yargs';
import path from 'path';

import { BatchError } from './subprocess';
import { setDefaultCwd } from './subprocess';
import { setDryRun } from './dry-run';

export const parserConfiguration = {
  'strip-aliased': true,
  'camel-case-expansion': false,
  'greedy-arrays': true,
  'populate--': true,
};

// eslint-disable-next-line @typescript-eslint/ban-types
type YargsType = Argv<{}>;

export function getYargs(yargs: YargsType, repoRoot: string): Argv<DefaultArgv> {
  return yargs
    .usage('$0 <command> [options]')
    .help('help', 'Show this help screen')
    .alias('help', 'h')
    .completion('completion', false)
    .option('dry-run', {
      default: false,
      description: 'Run without actually making modifications or destructive operations',
      global: true,
      group: 'Global',
      type: 'boolean',
    })
    .option('repo-root', {
      default: '',
      description: 'Path to the root of the repository',
      global: true,
      group: 'Global',
      hidden: true,
      normalize: true,
      type: 'string',
    })
    .option('verbosity', {
      alias: 'v',
      default: process.env.RJS_VERBOSITY ? parseInt(process.env.RJS_VERBOSITY) : 2,
      description: 'Set the verbosity of the script output. Use -v, -vv, or -vvv for more verbose',
      global: true,
      group: 'Global',
      type: 'count',
    })
    .option('ci', {
      default: false,
      description: 'Sets defaults for running scripts in a CI environment',
      global: true,
      group: 'Global',
      hidden: true,
      type: 'boolean',
    })
    .middleware(
      [
        (argv) => {
          argv['repo-root'] = String(argv['repo-root'] || repoRoot);
          setDefaultCwd(String(argv['repo-root']));
        },
        (argv) => {
          setDryRun(argv['dry-run']);
        },
        (argv) => {
          process.env.RJS_VERBOSITY = argv.verbosity.toString();
          if (argv.ci) {
            process.env.CI = 'true';
          }
        },
        () => {
          if (process.env.SUDO_UID) {
            const logger = getNodeLogger();
            logger.error(
              'Do not run commands with `sudo`! If elevated permissions are required, commands will prompt you for your password only if and when necessary.',
            );
            process.exit(1);
          }
        },
      ],
      true,
    )
    .wrap(Math.min(120, process.stdout.columns))
    .showHidden('show-advanced', 'Show advanced options')
    .group('show-advanced', 'Global')
    .global('show-advanced')
    .group('help', 'Global')
    .global('help')
    .strict()
    .parserConfiguration(parserConfiguration);
}

function fallbackHandler(argv: DefaultArgv) {
  const logger = getNodeLogger({ level: verbosityToLogLevel(argv.verbosity) });
  logger.info(argv);
  throw new Error('No handler defined');
}

export const commandDirOptions: RequireDirectoryOptions = {
  extensions: ['ts'],
  exclude: /(?:__tests__)/,
  recurse: false,
  visit: function visitor(module, filepath, filename) {
    const { handler, command, builder, ...rest } = module;
    const commandName = command || path.basename(filename || '', '.ts');
    return {
      ...rest,
      builder: builder || (((yargs) => yargs.strict(false)) as CommandBuilder),
      command: commandName,
      handler: async (argv: DefaultArgv) => {
        const logger = getNodeLogger({ level: verbosityToLogLevel(argv.verbosity) });
        logger.trace(`Resolved CLI arguments:
${Object.entries(argv).map((entry) => ` ${entry[0]}=${entry[1]}`)}`);

        process.on('unhandledRejection', (reason, promise) => {
          logger.error(`Unhandled Rejection at: ${promise} reason: ${reason}`);
        });

        try {
          if (handler) {
            await handler(argv);
          } else {
            fallbackHandler(argv);
          }
        } catch (err) {
          if (!(err instanceof BatchError)) {
            logger.error(err.message);
          }
          process.exitCode = 1;
        }
      },
    };
  },
};

export async function runBuilder<R = Record<string, unknown>>(
  builder: CommandBuilder<DefaultArgv, R>,
  cmd = '',
): Promise<Arguments<R>> {
  const [command, extra] = cmd.split(' -- ');
  const inputArgs = parser(`${command}${extra ? ` -- ${extra}` : ''}`, {
    configuration: parserConfiguration,
  });
  const yargs = Yargs(unparser(inputArgs));
  const middlewares: Array<MiddlewareFunction> = [];
  jest.spyOn(yargs, 'middleware').mockImplementation((middleware) => {
    middlewares.push(...(Array.isArray(middleware) ? middleware : [middleware]));
    return yargs;
  });

  if (typeof builder !== 'function') {
    throw new Error('Builder must be a function');
  }

  const processSpy = jest.spyOn(process, 'exit').mockImplementation(() => {
    throw new Error('failed');
  });

  const out = builder(
    getYargs(yargs, '/test-repo-root').middleware((argv) => {
      const level = verbosityToLogLevel(argv.verbosity);
      getNodeLogger({ level });
    }),
  );
  const resolvedOut = await (out instanceof Promise ? out : Promise.resolve(out));
  const argv = resolvedOut.argv;

  middlewares.forEach((m) => {
    m(argv);
  });

  processSpy.mockRestore();
  return argv;
}

export async function run<R = Record<string, unknown>>(
  builder: CommandBuilder<DefaultArgv, R>,
  handler: (args: Arguments<R>) => void | Promise<void>,
  cmd = '',
): Promise<void> {
  const argv = await runBuilder(builder, cmd);
  await handler(argv);
}

export function buildTest<R = Record<string, unknown>>(
  command: string,
  builder: CommandBuilder<DefaultArgv, R>,
  handler: (args: Arguments<R>) => void | Promise<void>,
) {
  return {
    build: async (cmd = '') => runBuilder<R>(builder, cmd),
    run: async (cmd = '') => run<R>(builder, handler, cmd),
  };
}
