import { isDryRun } from './dry-run';
import os from 'os';
import type { ChildProcess, SpawnOptions } from 'child_process';
import { spawn } from 'child_process';
import type { Logger } from '@zg-rentals/logger-node';
import getNodeLogger, { getChildLogger } from '@zg-rentals/logger-node';

export interface RunSpec {
  action: string;
  cmd: string;
  args?: Array<string>;
  opts?: SpawnOptions;
  logger?: Logger;
  blockInDryRun?: boolean;
}

let REPO_ROOT: string = process.cwd();

export function setDefaultCwd(cwd: string) {
  REPO_ROOT = cwd;
}

export class BatchError extends Error {}

function indent(str: string) {
  return ' > ' + str.trim().replace(/\n/g, '\n > ');
}

export async function run(options: RunSpec): Promise<{ output: string; subprocess?: ChildProcess }> {
  return new Promise(async (resolve, reject) => {
    const logger = options.logger || getChildLogger(options.action);

    const { blockInDryRun } = options;
    const commandText = `${options.cmd} ${(options.args || []).join(' ')}`;

    if (blockInDryRun && isDryRun()) {
      logger.info(`DRY-RUN Running command: ${commandText}`);
      return resolve({ output: '' });
    }

    logger.debug(`Running command: ${commandText}`);

    const { args = [], cmd, opts = {} } = options;

    const subprocess = spawn(cmd, args, {
      cwd: REPO_ROOT,
      ...opts,
      env: { ...process.env, ...(opts.env || {}) },
      stdio: opts.stdio || 'pipe',
    });

    let output = '';
    let error = '';

    function logOutput() {
      output && logger.info(`in subprocess:\n${indent(output)}`);
      error && logger.error(`in subprocess:\n${indent(error)}`);
    }

    subprocess.stdout?.on('data', (data) => {
      output += data.toString();
    });

    subprocess.stderr?.on('data', (data) => {
      error += data.toString();
    });

    subprocess.on('error', (error) => {
      logOutput();
      reject(error);
    });

    if (opts.detached) {
      subprocess.unref();
      resolve({
        output: '',
        subprocess,
      });
    } else {
      subprocess.on('close', (code) => {
        if (code && isFinite(code)) {
          logOutput();
          reject(new Error(`Subprocess failed with code ${code}`));
          return;
        }
        output = output.trim();
        if (output) {
          logger.debug(`Command output:\n${indent(output)}`);
        }
        resolve({
          output,
          subprocess,
        });
      });
    }
  });
}

type BuildPromises = Array<() => Promise<string | void>>;

export async function batch(processes: Array<RunSpec>, parallel = true): Promise<Array<string | void>> {
  const results: Array<string | void> = [];
  const logger = getNodeLogger();
  let failing = false;
  const buildPromises: BuildPromises = processes.map((process) => {
    return () => {
      return run({ ...process, logger })
        .then((output) => {
          logger.info(process.action);
          return output.output;
        })
        .catch((e) => {
          logger.error(process.action);
          if (e.message) {
            logger.error(e.message);
          }
          failing = true;
          throw e;
        });
    };
  });

  const maxParallel = parallel ? Math.min(os.cpus().length - 1, buildPromises.length) : 1;

  return new Promise((resolve, reject) => {
    function runTask(runner: () => Promise<string | void>): Promise<void> {
      return runner()
        .then((result) => {
          results.push(result);
          return;
        })
        .catch((e) => {
          results.push(e);
        })
        .finally(() => {
          runNextTask();
        });
    }

    function runNextTask() {
      if (buildPromises.length) {
        const runnable = buildPromises.shift();
        if (runnable) {
          runTask(runnable);
        }
      }

      if (!buildPromises.length && results.length === processes.length) {
        if (failing) {
          reject(new BatchError('Batch processes failed'));
        }
        resolve(results);
      }
    }

    buildPromises.splice(0, maxParallel).forEach((promise) => {
      runTask(promise);
    });
  });
}
